// ******************************************************************************************
//  RENAME EXPORT FUNCTION
// ******************************************************************************************
const renameExport = (props, state) => {
  let headersRow = [
     '1 DLMSONG TITLE', '2 DLM FILENAME (WAV)', '3 DLM FILENAME (MP3)', '4 NBC FILENAME (WAV)',
     '5 NBC FILENAME (MP3)', '6 PROTUNES/GMR', '7 MUSIC DIRECTOR', '8 ZINKO', '9 DLM CATEGORY',
     '10 DLM STYLE', '11 FOLDER', '12 RELEASE #', '13 TRACK ID#', '14 ISRC', '15 PNG MP3', '16 PNG WAV',
     '17 CUE RATING', '18 STYLE MD', '19 PROTUNES - UNDER 6 RATED TO REMOVE', '20 GROOVERS - UNDER 8 RATED TO REMOVE',
     '21 ACTIVE WAVs', '22 AVID MXF a1', '23 AVID MXF a2', '24 CUE_ID MXF a1', '25 CUE_ID MXF a2'
   ];

  let csvData = [];
  csvData.push(headersRow.join('\t'))
    props.selectedLibrary.library.filter(cue => {
      switch(state.batchOrRelease){
        case 'Background Instrumental Batch':
          return state.inclusive ? cue.rel_id <= state.releaseFilter.value && cue.cue_status !== 'Pulled' : cue.rel_id === state.releaseFilter.value && cue.cue_status !== 'Pulled';
          break;
        case 'Background Instrumental Release':
          let releasesArray = (/\-/).test(state.releaseFilter.value) ? state.releaseFilter.value.split('-') : state.releaseFilter.value;
          return isNaN(state.releaseFilter.value) ? cue.rel_id >= releasesArray[releasesArray.length-1] && cue.rel_id <= releasesArray[0] && cue.cue_status !== 'Pulled' : cue.cue_status !== 'Pulled'
        case 'Indie Artist Release':
          return state.inclusive ? cue.rel_id <= state.releaseFilter.value && cue.cue_status !== 'Pulled' : cue.rel_id === state.releaseFilter.value && cue.cue_status !== 'Pulled';
          break;
      }
   }).forEach(row => {
     let newRow = [
       // 1 DLMSONG TITLE
       row.cue_title,
       // 2 DLM FILENAME (WAV)
       `DLM - ${row.cue_title}.wav`,
       // 3 DLM FILENAME (MP3)
       `DLM - ${row.cue_title}.mp3`,
       // 4 NBC FILENAME (WAV)
       `DLM_${row.cue_title}.wav`,
       // 5 NBC FILENAME (MP3)
       `DLM_${row.cue_title}.mp3`,
       // 6 PROTUNES/GMR
       `DLM_${row.cue_title.replace(/\s/g, "_")}.wav`,
       // 7 MUSIC DIRECTOR
       `${row.cue_id}-${row.cue_title.toLowerCase().replace(/\s/g, '')}.wav`,
       // 8 ZINKO
       `DLM${row.style_id.toString().padStart(3, 0)}_${row.cue_id}_${row.cue_title.toLowerCase().replace(/\s/g, '')}.mp3`,
       // 9 DLM CATEGORY
       `${props.categories.filter(categories => categories.cat_id === row.cat_id).map(cat => cat.cat_name)}`,
       // 10 DLM STYLE
       `${props.styles.filter(styles => styles.style_id === row.style_id).map(style => style.style_name)}`,
       // 11 FOLDER
       `${props.categories.filter(categories => categories.cat_id === row.cat_id).map(cat => cat.cat_name)}\\${props.styles.filter(styles => styles.style_id === row.style_id).map(style => style.style_name)}`,
       // 12 RELEASE #
       row.rel_id,
       // 13 TRACK ID#
       row.cue_id,
       // 14 ISRC
       `US-RRD-${row.cue_reldate_h.substring(2, 4)}-${row.cue_id.toString().slice(1)}`,
       // 15 PNG MP3
       `${row.cue_id}.mp3`,
       // 16 PNG WAV
       `${row.cue_id}.wav`,
       // 17 CUE RATING
       row.cue_rating,
       // 18 STYLE MD
       `DLM${row.style_id.toString().padStart(3, 0)}_${row.cue_id}-${row.cue_title.toLowerCase().replace(/\s/g, '')}.wav`,
       // 19 PROTUNES - UNDER 6 RATED TO REMOVE
       row.cue_rating < 6 ? `pppppppDLM - ${row.cue_title}.wav` : '',
       // 20 GROOVERS - UNDER 8 RATED TO REMOVE
       row.cue_rating < 8 ? `gggggggDLM - ${row.cue_title}.wav` : '',
       // 21 ACTIVE WAVs
       row.cue_status === 'Active' ? `aaaaaaaDLM - ${row.cue_title}.wav` : '',
       // 22 AVID MXF a1
       `DLM - ${row.cue_title}_a1.mfx`,
       // 23 AVID MXF a2
       `DLM - ${row.cue_title}_a2.mfx`,
       // 24 CUE_ID MXF a1
       `${row.cue_title}_a1.mfx`,
       // 25 CUE_ID MXF a2
       `${row.cue_title}_a2.mfx`
     ]
     csvData.push(`${newRow.join('\t')}`)
   })
   generateDownload(csvData.join('\n'), `DLM_${state.releaseFilter.label + "_"}${state.inclusive ? 'INC_' : ''}RENAME_${moment().format('YYYY.MM.DD-HH_mm_ss')}.csv`);
}

// ******************************************************************************************
//  SOUND MINER EXPORT FUNCTION
// ******************************************************************************************

const soundMinerExport = (props, state) => {
  let headersRow = [
     'Filename', 'Manufacturer', 'Library', 'CDTitle', 'TrackTitle', 'Version', 'Description', 'Category', 'SubCategory',
     'FeaturedInstrument', 'Keywords', 'Composer', 'Artist', 'Publisher', 'Designer', 'BWDescription', 'BWOriginator',
     'BWOriginatorRef', 'BWTime', 'BWDate', 'Tempo', 'ReleaseDate', 'TrackYear', 'ISRC'
   ];

   let csvData = [];
   csvData.push(headersRow.join('\t'))
   props.selectedLibrary.library.filter(cue =>
       state.inclusive ? cue.rel_id <= state.releaseFilter.value && cue.cue_status !== 'Pulled' : cue.rel_id === state.releaseFilter.value && cue.cue_status !== 'Pulled'
   ).forEach(row => {
   // --------------------------------------------------------------------------------------------------
   // these little functions parse data to Title Case formatting
   // and remove empty keywords/instruments and tailing commas
   // --------------------------------------------------------------------------------------------------
     let descriptionString = parseData(row.cue_desc).join(', ');
     let instrumentsString = parseData(row.cue_instrus_edit).join(', ');
   // --------------------------------------------------------------------------------------------------
     let composerArray = props.selectedComposers.filter(composer => composer.cue_id === row.cue_id);
     let compString = '';
     let splitString = '';
     let pubString = '';
     for(let c in composerArray){
       pubString += `${composerArray[c].publisher_name}`;
       pubString += c < composerArray.length - 1 ? ' / ' : ' ';
       compString += `${composerArray[c].last}${composerArray[c].suffix ? ' ' + composerArray[c].suffix : ''}, ${composerArray[c].first} ${composerArray[c].middle || ''} (${composerArray[c].pro_name})`;
       compString += c < composerArray.length - 1 ? ' / ' : ' ';
       splitString += `${composerArray[c].composer_split}`
       splitString += c < composerArray.length - 1 ? '/' : '%';
     }
     let wavRow = [
       // Filename
       `DLM - ${row.cue_title}.wav`,
       // Manufacturer
       `DL Music`,
       // Library
       `${compString}${splitString}`,
       // CDTitle
       row.cat_id !==19 && row.style_id !== 147 ? `${props.categories.filter(cat => cat.cat_id === row.cat_id).map(cat => cat.cat_name)}, ${props.styles.filter(style => style.style_id === row.style_id).map(style => style.style_name)}` : '',
       // TrackTitle
       row.cue_title,
       // Version
       (/\sv[0-9]{1,2}/).test(row.cue_title) ?
       row.cue_title.split(/\sv[0-9]{1,2}/)[1].replace(/\s?[()]/g, '') : "",
       // Description
       pubString,
       // Category
       `${props.categories.filter(cat => cat.cat_id === row.cat_id).map(cat => cat.cat_name)}`,
       // SubCategory
       `${props.styles.filter(style => style.style_id === row.style_id).map(style => style.style_name)}`,
       // FeaturedInstrument
       instrumentsString || '',
       // Keywords
       descriptionString,
       // Composer
       `${compString}${splitString}`,
       // Artist
       `${compString}${splitString}`,
       // Publisher
       pubString,
       // DESIGNER
       pubString,
       // BWDescription
       pubString,
       // BWOriginator
       'DL-Music.com',
       // BWOriginator Reference
       `US-RRD-${row.cue_reldate_h.substring(2, 4)}-${row.cue_id.toString().slice(1)}`,
       // BWTime
       row.cue_reldate_h.includes('T') ? row.cue_reldate_h.split('T')[1].split('.')[0] : row.cue_reldate_h.split(' ')[1].split('.')[0],
       // BWDate
       row.cue_reldate_h.split('T')[0],
       // Tempo
       props.tempos.filter(tempo => tempo.tempo_id === row.tempo_id)[0].tempo_name,
       // ReleaseDate
       row.cue_reldate_h.split('T')[0],
       // TrackYear
       row.cue_reldate_h.substring(0, 4),
       // ISRC
       `US-RRD-${row.cue_reldate_h.substring(2, 4)}-${row.cue_id.toString().slice(1)}`
     ]
     csvData.push(`${wavRow.join('\t')}`)
     wavRow.shift();
     csvData.push([`DLM - ${row.cue_title}.aif`, ...wavRow].join('\t'))
     csvData.push([`DLM - ${row.cue_title}.mp3`, ...wavRow].join('\t'))
   })
   generateDownload(csvData.join('\n'), `DLM_${state.releaseFilter.label + "_"}SOUNDMINER_EXPORT_${moment().format('YYYY.MM.DD-HH_mm_ss')}.txt`);
}

