import React from 'react';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom';
import moment from 'moment';
import Select from 'react-select';
import Loader from './SubComponents/Loader';
import { initializeSelectedLibrary } from '../actions/selectedLibraryActions';
import { initializeSelectedComposer } from '../actions/selectedComposerActions';
import { initializeSelectedReleases } from '../actions/selectedReleasesActions';

// this import contains all the export/download functions:
const exportTools = require('./MetaDataExportFunctions/ExportTools.js');

class ExportDashboard extends React.Component {
   constructor(props) {
     super(props)
   }

   state = {
     batchesDropDown: [],
     batchOrRelease: 'Background Instrumental Batch',
     inclusive: false,
     releaseFilter: 147,
     releasesDropDown: [],
     selectedLibrary: 'background-instrumentals'
   }

   handleChange = (releaseFilter) => {
     this.setState({releaseFilter})
   }

   handleCheck = () => {
     this.setState({inclusive: !this.state.inclusive})
   }

  handleRadio = (event) => {
    switch(event.target.value){
      case 'background-instrumentals':
        this.setState(
          {
          batchesDropDown: this.props.batchesBI.map(rel => {
            return {value: rel.rel_id, label: rel.rel_num}}),
          batchOrRelease: 'Background Instrumental Batch',
          releaseFilter: null,
          selectedLibrary: event.target.value
       })
        this.props.initializeSelectedLibrary(this.props.cues, event.target.value);
        this.props.initializeSelectedComposer(this.props.composersBI)
        this.props.initializeSelectedReleases(this.props.batchesBI)
        break;
      case 'independent-artists':
        this.setState({
            batchesDropDown: this.props.releasesIA.map(rel => {
          return {value: rel.rel_id, label: rel.rel_num}}),
            batchOrRelease: 'Indie Artist Release',
            releaseFilter: null,
            selectedLibrary: event.target.value
          })
        this.props.initializeSelectedLibrary(this.props.tracks, event.target.value);
        this.props.initializeSelectedComposer(this.props.artists)
        this.props.initializeSelectedReleases(this.props.releasesIA)
        break;
      case 'Background Instrumental Release':
        this.setState({
          batchOrRelease: event.target.value,
          batchesDropDown: this.state.releasesDropDown,
          releaseFilter: null,
          })
        break;
      case 'Background Instrumental Batch':
        this.setState(
          {batchOrRelease: event.target.value,
            batchesDropDown: this.props.batchesBI.map(rel => {
            return {value: rel.rel_id, label: rel.rel_num}
          }),
          releaseFilter: null})
        break;
      default:
        null
    }
  }

  async componentDidMount (){
    // initialize the library in case this is the landing page
    if(this.props.selectedLibrary.library.length === 0){
      this.props.initializeSelectedLibrary(this.props.cues, 'background-instrumentals')
    }

    await this.setState({batchesDropDown: this.props.batchesBI.map(rel => {
      return {value: rel.rel_id, label: rel.rel_num}
    })})
    if(this.props.composersBI.length === 0){
      await this.props.initializeSelectedComposer(this.props.composersBI)
    }
    if(this.props.batchesBI.length === 0){
      await this.props.initializeSelectedReleases(this.props.batchesBI)
    }
    // this handles creating the releasesDropDown array
    if(this.state.releasesDropDown.length === 0){
      var releases = {};
      for (var i = 0; i < this.props.batchesBI.length; i++) {
        var releaseLabel = `${isNaN(parseInt(this.props.batchesBI[i].rel_num_only)) ? '': 'R' }${this.props.batchesBI[i].rel_num_only}`;
        if (!releases[releaseLabel]) {
          releases[releaseLabel] = [];
        }
        releases[releaseLabel].push(this.props.batchesBI[i].rel_id);
      }
      let releasesDropDown = [];
      for (var releaseLabel in releases) {
        releasesDropDown.push({releaseLabel: releaseLabel, releaseIDs: releases[releaseLabel].join('-')});
      }
      this.setState({releasesDropDown: releasesDropDown.map(rel => {
        return {value: rel.releaseIDs, label: rel.releaseLabel}
      })})
    }
  }

  componentDidUpdate(prevProps, prevState) {
    // this is initializing our selected composers by default once the API request is returned
    // I added this condition so that the user could start on any /url_path
    if(this.props.selectedComposers.length === 0){
      this.props.initializeSelectedComposer(this.props.composersBI)
    }
  }

  render(){
    console.log(123, this.props)
    console.log(124, this.state)

    const releaseFilter  = this.state.releaseFilter;
    const selectedLibrary = this.props.selectedLibrary.library;
    const ExportsOptions = () => {
     return(
     <div className='column-wrapper'>
       <div className='dashboard-left-column'>
         <h2>Meta Data Dashboard</h2>
         <h3>Filter Options: </h3>
         <Select
           value={releaseFilter}
           onChange={this.handleChange}
           options={this.state.batchesDropDown}
           className='exports-dropdown'
           placeholder={ this.state.batchOrRelease === 'Indie Artist Release' ? `Select an ${this.state.batchOrRelease}` : `Select a ${this.state.batchOrRelease}`}
         />
         <br/>
         <label>
           <input
             type='radio'
             name='react-tips'
             value='background-instrumentals'
             checked={this.state.selectedLibrary === 'background-instrumentals'}
             className='form-check-input'
             onChange={this.handleRadio}
           /> Background Instrumentals
         </label>
         <label>
           <input
             type='radio'
             name='react-tips'
             value='independent-artists'
             checked={this.state.selectedLibrary === 'independent-artists'}
             className='form-check-input'
             onChange={this.handleRadio}
           /> Independent Artists
         </label>
         <br/>

         {/* conditionally render these radios if we are searching through the BI catalog */}
         {this.state.selectedLibrary === 'background-instrumentals' ?
           <div>
             <label>
               <input
                 type='radio'
                 name='releases-or-batches'
                 value='Background Instrumental Batch'
                 checked={this.state.batchOrRelease === 'Background Instrumental Batch'}
                 className='form-check-input'
                 onChange={this.handleRadio}
               /> Batches
             </label>
             <label>
               <input
                 type='radio'
                 name='releases-or-batches'
                 value='Background Instrumental Release'
                 checked={this.state.batchOrRelease === 'Background Instrumental Release'}
                 className='form-check-input'
                 onChange={this.handleRadio}
               /> Releases
             </label>
           </div>
         : null
         }
         {/********************** end of conditionally rendering radios **********************/}
         <br/>
         <input type="checkbox" id="inclusive" onChange={this.handleCheck} checked={this.state.inclusive}/> Inclusive?
         <br/>
         (checking this will include all releases before the one that's selected)
       </div>
       <div className='dashboard-right-column'>
         <h2>Download Links:</h2>
         <a onClick={(() => exportTools.renameExport(this.props, this.state))} className='select'>{`Rename Export ${this.state.releaseFilter && this.state.releaseFilter.label ? this.state.releaseFilter.label : ''} ${this.state.inclusive ? 'INC' : ''}`}</a>
         <br/>
         <a onClick={(() => exportTools.soundMinerExport(this.props, this.state))} className='select'>{`SoundMiner Batch Export ${this.state.releaseFilter && this.state.releaseFilter.label ? this.state.releaseFilter.label : ''} ${this.state.inclusive ? 'INC' : ''}`}</a>
         <br/>
         <a onClick={(()=> exportTools.nbcSoundMinerExport(this.props, this.state))} className='select'>{`NBCU SoundMiner Batch Export ${this.state.releaseFilter && this.state.releaseFilter.label ? this.state.releaseFilter.label : ''} ${this.state.inclusive ? 'INC' : ''}`}</a>
         <br/>
         <a onClick={(()=> exportTools.proTunesExport(this.props, this.state))} className='select'>{`ProTunes Batch Export ${this.state.releaseFilter && this.state.releaseFilter.label ? this.state.releaseFilter.label : ''} ${this.state.inclusive ? 'INC' : ''}`}</a>
         <br/>
         <a onClick={(()=> exportTools.alterKExport(this.props, this.state))} className='select'>{`Alter K Release Export ${this.state.releaseFilter && this.state.releaseFilter.label ? this.state.releaseFilter.label : ''} ${this.state.inclusive ? 'INC' : ''}`}</a>
         <br/>
         <a onClick={(()=> exportTools.sourceAudioExport(this.props, this.state))} className='select'>{`SourceAudio Batch Export ${this.state.releaseFilter && this.state.releaseFilter.label ? this.state.releaseFilter.label : ''} ${this.state.inclusive ? 'INC' : ''}`}</a>
       </div>
     </div>
    )}
    return(
    <div className='dashboard'>
     { this.props.selectedComposers.length === 0 &&
       selectedLibrary.length === 0
         ? <Loader/> : <ExportsOptions/>}
    </div>
  )}
}

const mapStateToProps = (state) => {
  return {
    artists: state.artists,
    BImasterIDs: state.BImasterIDs,
    categories: state.categories,
    composersBI: state.composersBI,
    cues: state.cues,
    instrumentsBI: state.instrumentsBI,
    keywordsBI: state.keywordsBI,
    batchesBI: state.batchesBI,
    releasesIA: state.releasesIA,
    selectedComposers: state.selectedComposers,
    selectedLibrary: state.selectedLibrary,
    selectedReleases: state.selectedReleases,
    styles: state.styles,
    tempos: state.tempos,
    tracks: state.tracks,
  };
}

const mapDispatchToProps = {
  initializeSelectedLibrary,
  initializeSelectedComposer,
  initializeSelectedReleases
}


export default connect(mapStateToProps, mapDispatchToProps)(ExportDashboard);
